{/*Search Eng */}
class Search extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      input: ""
    }
  }
  handleChange(event) {
    this.setState({
      input: event.target.value
    })
  }
  render() {
    return(
      <div>
        <input value = {this.state.input} onChange = {this.handleChange.bind(this)}/>
        <SearchResult output={this.state.input}/>
      </div>
    )
  }
}

class SearchResult extends React.Component {
  constructor(props) {
    super(props)
  }
  render() {
    function searchLib(link,src, alt, title, category = []) {
      const categories = category.map(e => <p className="category">{e}</p>)
      return (
        <div className="srcAnim">
          <a href={link}>
        <img style={{width:250},{height: 150}} src={src} alt={alt} />
        </a>
        <div>
        <h3 className="title" style={{paddingRight:30}}>{title}</h3>
        <div className="categories">
          {categories}
        </div>
        </div>
        
        </div>
      )
    }
    var searchLi = {
      0: ["naruto", searchLib("https://asenshu.com/seriale/naruto/",naruto, "naruto", "Naruto", ["Action", "Adventure"])],
      1: ["attack on titan", searchLib("https://asenshu.com/seriale/attack-on-titan-ova/",aot, "attackOnTitan", "Attack On Titan",["Action", "Drama", "Adventure"] )],
      2: ["my hero academia", searchLib("https://asenshu.com/seriale/my-hero-academia/",mha, "mhaImg", "My Hero Academia", ["Action", "Drama"])],
      3: ["jujutsu kaisen", searchLib("https://www1.kissanimes.cc/watch-anime/jujutsu-kaisen-tv",jk, "jkImg", "Jujutsu Kaisen",["Action"])],
      4: ["one punch man", searchLib("https://asenshu.com/seriale/one-punch-man/",opm, "opmImg", "One Punch Man", ["Action", "Comedy"])],
      5:["the seven deadly sins", searchLib("https://asenshu.com/seriale/the-seven-deadly-sins/",sevenDeadly, "sdsImg", "The Seven Deadly Sins", ["Action", "Drama"])],
      6: ["dragon ball", searchLib("https://asenshu.com/seriale/dragon-ball-z-kai/",db, "dbImg", "Dragon Ball"["Action"])],
      7: ["kakeguri", searchLib("https://gototub.com/series/kakegurui/",kakegur, "kakeguri", "Kakeguri",["Drama"])],
      8: ["fate", searchLib("https://www.justwatch.com/us/tv-show/fate-stay-night-unlimited-blade-works",fsn, "fateStayNight", "Fate/StayNight: Ultimated Blade Works", ["Action"])],
      9: ["devil man", searchLib("https://ww3.9movies.yt/watch/devilman-crybaby-19218/?ep=45018",devilM, "devilMan", "DevilMan Crybaby",["Action", "Adventure"])],
      10: ["sword art online", searchLib("https://watch-sao.com/",sao, "saoImg", "Sword Art Online", ["Action", "Adventure"])]
  }
  var display = []
  for(let i = 0; i <= 10; i++) {
    if(searchLi[i][0] === this.props.output) {
      display.push(searchLi[i][1])
    }
  }
    return(
      <div className="searchOutput">
        <p style={{display: "none"}}>{this.props.output}</p>
        {display}
      </div>
    )
  }
}

{/*Library*/}


const Library =(props) => {
  
  
    function libraryStructure(link,id, src, alt, category=[], title) {
      
      const categories = category.map(event=> <p className="category">{event}</p>)
      return(<div className="anime" id={id}>
        <a href={link} target="_blank">
        <img id="libImg" src={src} alt={alt} />
        <div className="categories">
          {categories}
        </div>
        <h3 className="title">{title}</h3>
        </a>
      </div>)
}
    var libr= [ 
    [["Action", "Adventure"],libraryStructure("https://asenshu.com/seriale/naruto/","Naruto", naruto, "Naruto", ["Action", "Adventure"], "Naruto")],
    [["Action", "Drama", "Adventure"],libraryStructure("https://asenshu.com/seriale/attack-on-titan-ova/","aot", aot, "attackOnTitan", ["Action", "Drama", "Adventure"], "Attack On Titan")],
    [["Action", "Drama"],libraryStructure("https://asenshu.com/seriale/my-hero-academia/","mha", mha, "myHeroAcademy", ["Action", "Drama"], "My Hero Academia")],
    [["Action"],libraryStructure("https://www1.kissanimes.cc/watch-anime/jujutsu-kaisen-tv","jk", jk, "jujutsu kaisen", ["Action"], "Jujutsu Kaisen")],
    [["Action", "Comedy"],libraryStructure("https://asenshu.com/seriale/one-punch-man/","opm", opm, "One Punch Man", ["Action", "Comedy"], "One Punch Man")],
    [["Action", "Drama"],libraryStructure("https://asenshu.com/seriale/the-seven-deadly-sins/","sds", sevenDeadly, "theSevenDeadlySins", ["Action", "Drama"], "The Seven Deadly Sins")],
    [["Action"],libraryStructure("https://asenshu.com/seriale/dragon-ball-z-kai/","db", db, "DragonBall", ["Action"], "Dragon Ball")],
    [["Drama"],libraryStructure("https://gototub.com/series/kakegurui/","kakeguri", kakegur, "Kakeguri", ["Drama"], "Kakeguri")],
    [["Action"],libraryStructure("https://www.justwatch.com/us/tv-show/fate-stay-night-unlimited-blade-works","fsn", fsn, "fsn", ["Action"], "Fate/Stay Night: Ultimated Blade Works")],
    [["Action", "Adventure"],libraryStructure("https://ww3.9movies.yt/watch/devilman-crybaby-19218/?ep=45018","devilM", devilM, "devilMan", ["Action", "Adventure"], "Devilman Crybaby")],
    [["Action", "Adventure"],libraryStructure("https://watch-sao.com/","sao", sao, "saoImg", ["Action", "Adventure"], "Sword Art Online")]
]

var libraryDisplay = [];
for(let i = 0; i < libr.length; i++) {
  for (let j = 0; j <libr[i][0].length; j++){
      if(props.cat === "Action" && libr[i][0][j] === "Action") {
        libraryDisplay.push(libr[i][1])
      }}
      if(props.cat === "") {
        libraryDisplay.push(libr[i][1])
      }
      
    }
    
    return(
      <div id="library">
        {libraryDisplay}
        <p style={{display: "none"}}>{props.cat}</p>
      </div>
    )
}

Library.defaultProps = {
  cat: ""
}



export default App;
